<template>
  <div class="row" v-if="isReady">
    <div class="col-12 flex-x-between mt-3">
      <div class="flex-align-center">
        <el-button
          v-ripple.400
          circle
          type="primary"
          class="no-width mr-2 flat"
          @click="$router.go(-1)"
        >
          <i class="bx bx-left-arrow-alt"></i>
        </el-button>
        <h5 class="my-0">Customers</h5>
      </div>

      <div class="flex-align-center flex-end">
        <el-button
          class="mx-0"
          type="primary"
          @click="$router.push('/settings/add-new-customer')"
        >
          Add customer
        </el-button>
      </div>
    </div>
    <div class="col-12 list-table mt-3">
      <div class="box pt-3">
        <div class="row">
          <div class="col-12 flex-align-center">
            <p class="mx-0 table-tab active-table-tab">All</p>
          </div>
          <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div>
          <div
            class="
              col-12
              mb-1
              d-flex
              justify-content-between
              align-items-center
            "
          >
            <div class="flex-align-center">
              <el-select
                v-model="filter.pageSize"
                class="mr-2 page-size-selector mr-2"
              >
                <el-option
                  v-for="page in 10"
                  :key="page"
                  :label="page * 5"
                  :value="page * 5"
                >
                </el-option>
              </el-select>
              <el-input
                v-model="filter.query"
                class="mr-2 table-search"
                placeholder="Type something"
                prefix-icon="bx bx-search"
              >
              </el-input>
            </div>

            <el-popover placement="bottom" width="200" trigger="click">
              <p class="font-weight-bold">Sort By</p>
              <div class="b-divider my-2"></div>
              <div class="w-100">
                <el-radio
                  v-model="filter.order_by"
                  v-for="(sort, n) in sortList"
                  :key="sort.name"
                  :class="n !== sortList.length - 1 && 'mb-3'"
                  :label="sort.value"
                >
                  {{ sort.name }}
                </el-radio>
              </div>
              <el-button
                class="mr-0 flat no-width"
                v-ripple.400
                slot="reference"
                type="dark"
              >
                <i class="bx bx-sort mr-1"></i> Sort
              </el-button>
            </el-popover>
          </div>
        </div>

        <div class="w-100 el-main-table" v-loading="loading">
          <div class="el-table-header" v-if="selected.length > 0">
            <div class="d-flex align-items-center">
              <el-checkbox
                :indeterminate="selected.length == customers.results.length"
                v-model="allCheck"
                @change="$refs.multipleTable.toggleAllSelection"
                >{{ selected.length }} selected</el-checkbox
              >
              <el-dropdown @command="changeAction" trigger="click">
                <p
                  class="el-dropdown-link ml-3 pointer primary hover-underline"
                >
                  Actions
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </p>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    v-for="action in actions"
                    :key="action.value"
                    :command="action.value"
                  >
                    {{ action.label }}</el-dropdown-item
                  >
                </el-dropdown-menu>
              </el-dropdown>
            </div>
          </div>
          <el-table
            ref="multipleTable"
            :data="customers.results"
            style="width: 100%"
            @row-click="
              (row) =>
                $router.push({
                  name: 'Edit Customer',
                  params: { id: row.id },
                })
            "
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection"> </el-table-column>
            <el-table-column property="name" label="Name"> </el-table-column>
            <el-table-column property="email" label="Email"> </el-table-column>
            <el-table-column property="phone" label="Phone"> </el-table-column>
            <el-table-column label="Sales">
              <template slot-scope="scope">
                {{ scope.row.sales.length }} sales placed
              </template>
            </el-table-column>
            <template #empty>
              <el-empty class="w-100" description="No item found!">
                <template #image> <empty /> </template
              ></el-empty>
            </template>
          </el-table>
          <div class="el-table-footer" v-if="customers.total_pages !== 1">
            <el-pagination
              background
              @current-change="
                (index) => ((customers.current_page = index), getCustomers())
              "
              :current-page="customers.current_page"
              layout="prev, pager, next"
              :page-count="customers.total_pages"
            >
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { empty } from "../../common";

export default {
  data() {
    return {
      allCheck: false,
      search: "",
      page: 1,
      max: 5,
      active: 0,
      selected: [],
      removing: false,
      customers: {
        current_page: 1,
        results: [],
      },
      filter: {
        query: "",
        order_by: "-updated_at",
        pageSize: 10,
        supplier: "",
        status: "",
      },
      actions: [
        // {
        //   label: "Delete customers",
        //   value: "delete",
        // },
      ],
      sortList: [
        {
          name: "Name A–Z",
          value: "name",
        },
        {
          name: "Name Z–A",
          value: "-name",
        },
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
      ],
      loading: true,
    };
  },
  created() {
    this.$store.commit("setIsReady", false);
    this.getCustomers();
  },
  watch: {
    filter: {
      handler: function () {
        this.getCustomers();
      },
      deep: true,
    },
  },
  computed: {
    ...mapState(["isReady"]),
  },
  components: {
    empty,
  },
  methods: {
    changeAction(action) {
      if (action === "delete") {
        this.removeCustomers();
      }
    },
    getCustomers() {
      this.loading = true;
      var link = `customers/?page=${this.customers.current_page}&query=${this.filter.query}&order_by=${this.filter.order_by}&page_size=${this.filter.pageSize}`;
      this.$axios
        .get(link)
        .then((response) => {
          this.loading = false;
          this.customers.results = response.data.results;
          this.customers.total_pages = response.data.total_pages;

          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.selected = val;
    },
    removeCustomers() {
      // if (this.selected.length > 0) {
      //   this.removing = true;
      //   var data = [];
      //   this.selected.forEach((customer) => {
      //     data.push(customer.id);
      //   });
      //   this.$axios.post("remove-customers/", { data }).then(() => {
      //     this.getCustomers();
      //     this.selected = [];
      //     this.removing = false;
      //     this.$vs.notification({
      //       color: "success",
      //       title: "Success",
      //       text: "Cool! customer has been successfully deleted.",
      //     });
      //   });
      // }
    },
  },
};
</script>

